.contact-section {
    margin-top: 76px;
    padding: 4rem 0;
    background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.9)),
                url('../images/banner.png');
    background-size: cover;
    background-position: center;
    min-height: calc(100vh - 76px);
}

.contact-card {
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    background: white;
    padding: 2rem;
}

.contact-info-card {
    background: var(--primary-color);
    color: white;
    border-radius: 15px;
    padding: 2rem;
}

.contact-info-card h3,
.contact-info-card h4 {
    color: var(--accent-color);
}

.contact-info-card hr {
    border-color: var(--accent-color);
    opacity: 0.3;
}

.volunteer-options {
    background: var(--light-color);
    border-radius: 15px;
    padding: 2rem;
    margin-top: 3rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.volunteer-options h3 {
    color: var(--primary-color);
}

.volunteer-options h4 {
    color: var(--primary-color);
    font-size: 1.2rem;
    margin-bottom: 1rem;
}

.volunteer-options p {
    color: var(--dark-color);
    opacity: 0.8;
}

.form-control {
    border-radius: 8px;
    padding: 0.75rem;
    border: 1px solid rgba(0, 0, 0, 0.1);
}

.form-control:focus {
    border-color: var(--primary-color);
    box-shadow: 0 0 0 0.2rem rgba(0, 43, 92, 0.25);
}

textarea.form-control {
    min-height: 120px;
}

.form-label {
    color: var(--primary-color);
    font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
    .contact-section {
        padding: 2rem 1rem;
    }

    .contact-card,
    .contact-info-card,
    .volunteer-options {
        padding: 1.5rem;
        margin-bottom: 1.5rem;
    }

    .btn-lg {
        width: 100%;
        margin-bottom: 1rem;
    }
}
